import { Request, Response } from 'express';
import ProductService from '@/services/product.service';
import { ResponsePattern } from '@/interfaces/response.interface';

class ProductController {
  public productService = new ProductService();

  public readProduct = async (req: Request, res: Response): Promise<void> => {
    try {
      const productId = BigInt(req.params.id);
      const response: ResponsePattern = await this.productService.readProduct(productId, 'id, code, name, image');
      res.status(response.statusCode).json(response);
    } catch (e) {
      console.log(e);
      res.status(400).json({ statusCode: 400, message: 'Bad Request' });
    }
  };

  public createProduct = async () => {};
}

export default ProductController;
